.App {
  text-align: center;
  height: 100%
}

.App-header {
  background-color: #121212;
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  gap: 1rem;
  font-size: 2em;
  padding: 2rem;
  padding-bottom: 0;
}

.Posting-Space {
  width: 100%;
}

.body {
  display: flex;
  flex-direction: row;
  background-color: #121212;
  color: white;
  gap: 2rem;
  padding: 2rem;
  margin: 0;
  min-height: 100%;
}